<template>
  <div>
    <main> <!-- Début du main -->
      <div class="title"> <!-- title de présentation, dans le main -->
        <h1>Éléments supprimés</h1>
      </div>
      <div class="trash">
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="trashLoading"></BarLoader>
        <Element v-for="element in getTrash" v-bind="element" :key="element.id" actions="true"></Element>
        <h2 v-if="getTrash.length == 0">La corbeille est vide</h2>
      </div>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import Element from '@/components/card/element.vue'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'home',
  components: {
    Element
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchTrash()
  },
  computed: {
      ...mapGetters('trash', [
        'getTrash',
        'trashLoading'
      ]),
      ...mapGetters('jwt', [
        'jwt'
      ])
  },
  methods: {
    ...mapActions('trash', [
      `fetchTrash`,
      `restoreElement`,
      `deleteElement`
    ]),
    ...mapActions('alert', [
      'newAlert'
    ])
  }
}
</script>
